import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

// Additional plugins
import VueCookies from 'vue-cookies'

// Using plugins
Vue.use(VueCookies)

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
