import Vue from 'vue'
import Vuex from 'vuex'

import vuexI18n from 'vuex-i18n'

import translationsEn from '@/locale/en'
import translationsRu from '@/locale/ru'
import translationsDe from '@/locale/de'
import translationsFr from '@/locale/fr'
import translationsIt from '@/locale/it'

Vue.use(Vuex)

// States
const state = {
  curLanguage: null,
  languages: [
    {
      short: 'en',
      long: 'English'
    },
    {
      short: 'ru',
      long: 'Русский'
    },
    {
      short: 'de',
      long: 'Deutsch'
    },
    {
      short: 'fr',
      long: 'Français'
    },
    {
      short: 'it',
      long: 'Italiano'
    }
  ]
}

// Set default 'en' locale
state.curLanguage = state.languages[0]

// Getters
const getters = {
  // Empty
}

// Mutations
const mutations = {
  setLanguage (state, lang) {
    state.curLanguage = lang
  }
}

// Actions
const actions = {
  // Empty  
}

// Modules
const modules = {
  // Empty  
}

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  modules
})

Vue.use(vuexI18n.plugin, store)

Vue.i18n.add('en', translationsEn)
Vue.i18n.add('ru', translationsRu)
Vue.i18n.add('de', translationsDe)
Vue.i18n.add('fr', translationsFr)
Vue.i18n.add('it', translationsIt)

export default store
