<template>
  <div v-show="!isAccepted" class="cookies-accept">
    <p class="cookies-accept__text">
      {{ 'cookiesAcceptText' | translate }}
    </p>
    <div class="cookies-accept__button">
      <button
        type="button"
        class="ir-blue-button accept-button"
        @click="accept"
      >
        {{ 'cookiesAcceptButtonText' | translate }}
      </button>
      <span class="decorate-line"></span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CookiesAccept',
  data () {
    return {
      isAccepted: false
    }
  },
  methods: {
    accept () {
      this.isAccepted = true
      this.$cookies.set('cookies-accept', this.isAccepted)
    }
  },
  created () {
    this.isAccepted = this.$cookies.get('cookies-accept')
  }
}
</script>

