<template>
  <div id="app">
    <router-view/>
    <CookiesAccept />
  </div>
</template>

<script>
import Vue from 'vue'
import { mapState } from 'vuex'

// Components
import CookiesAccept from '@/components/CookiesAccept.vue'

export default {
  name: 'app',
  components: {
    CookiesAccept
  },
  data () {
		return {
			selectedLanguage: this.$store.state.curLanguage,
		}
	},
  computed: {
    ...mapState(['languages'])
  },
  created () {
    // Define language
    let lang = this.$cookies.get('lang')

    const languages = this.languages.map(elem => elem.short)
    if (!lang || languages.indexOf(lang) === -1) lang = 'en'

    Vue.i18n.set(lang)
		this.$store.commit('setLanguage', lang)
  }
}
</script>

