</template>

<script>
import VideoBox from '@/components/VideoBox.vue'

export default {
  name: 'PageHome',
  components: {
    VideoBox
  },
  data () {
    return {
      showIRVideo: false,
      showPEVideo: false
    }
  },
  methods: {
    goTo (flashmob) {
      console.log(flashmob)
      if (flashmob === 'ir') {
        this.showIRVideo = true
        setTimeout(() => {
          this.showIRVideo = true
          this.$router.push('/ir')
        }, 5000)
      }

      if (flashmob === 'pe') {
        this.$router.push('/pe')
      }

    }
  }
}
</script>

