import Vue from 'vue'
import VueRouter from 'vue-router'
import PageHome from '../views/PageHome.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'PageHome',
    component: PageHome
  },
  {
    path: '/register',
    name: 'PageRegister',
    component: () => import(/* webpackChunkName: "register" */ '../views/PageRegister.vue')
  },
  {
    path: '/confirm-register',
    name: 'PageConfirmRegister',
    component: () => import(/* webpackChunkName: "register" */ '../views/PageConfirmRegister.vue')
  },
  {
    path: '/login',
    name: 'PageLogin',
    component: () => import(/* webpackChunkName: "login" */ '../views/PageLogin.vue')
  },
  {
    path: '/password-recovery',
    name: 'PagePasswordRecovery',
    props: (route) => ({ token: route.query.token }),
    component: () => import(/* webpackChunkName: "login" */ '../views/PagePasswordRecovery.vue')
  },
  {
    path: '/ir',
    name: 'PageIR',
    component: () => import(/* webpackChunkName: "immortal-regiment" */ '../views/PageIR.vue')
  },
  {
    path: '/pe',
    name: 'PagePE',
    component: () => import(/* webpackChunkName: "planet-earth" */ '../views/PagePE.vue')
  },
  {
    path: '/terms',
    name: 'PageTerms',
    component: () => import(/* webpackChunkName: "terms" */ '../views/PageTerms.vue')
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
